*,
*::before,
*::after {
  margin: 0;
  padding: 0;
}

body {
  font-family: Montserrat, sans-serif;
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 1.66;
  letter-spacing: 0.08em;
  color: $text-color;

  @media screen and (min-width: 1400px) {
    font-size: 18px;
    line-height: 1.55;
  }
}

.text-dark {
  color: $secondary-text-color;
}

.bold-text {
  font-weight: 700;
  font-size: 14px;
  line-height: 1.43;
  color: $primary-text-color;

  @media screen and (min-width: 1400px) {
    font-size: 20px;
    line-height: 1.4;
  }
}

// .text-color {
//   color: $accent-color;
// }

img {
  display: block;
  width: 100%;
  height: auto;
}

.list {
  padding: 0;
  margin: 0;
  list-style: none;
}

.link {
  text-decoration: none;
}

.title {
  @include font(20px, 800, 27px);
  color: $primary-text-color;
  letter-spacing: 0.03em;

  @media screen and (min-width: 768px) {
    @include font(26px, 800, 34px);
  }

  @media screen and (min-width: 1400px) {
    @include font(46px, 800, 60px);
  }
}

.black-background {
  color: $white-color;
}
